import React from "react"
import Link from 'gatsby-link';
import LoginPreview from '../pages/images/login.jpg';
import RacePreview from '../pages/images/race_selection.jpg';
import ClassPreview from '../pages/images/class_selection.jpg';
import NewCharacter from '../pages/images/new_character.jpg';

export default () =>
<div>
    <div className="jumbotron connect">
        <div className="container">
            <h1>Play Return of the Shadow!</h1>
            <p>Connect at rotsmud.dyndns.org port 3791</p>
        </div>
    </div>
    <section className="play">
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <h1>Getting Started</h1>
                    <p>
                        Remember to check out our <Link to="/getting-started">Getting Started page</Link> for a lot of userful information once you have connected to RotS!
                    </p>
                </div>
            </div>
        </div>
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <h1>With a MUD Client</h1>
                    <p>This is the most powerful option, but it may require installing and configuring software on your comptuer.</p>
                    <p>
                        If you already have a MUD client, just connect to <strong>rotsmud.dyndns.org</strong> on port <strong>3791</strong> and you are ready to go. 
                    </p>
                    <p>Once you've connected to RotS you'll be presented with a Login Screen</p>
                    <img className="play-img" src={LoginPreview} />
                    <p>Enter the name you want to be called. </p>
                    <p>You'll need be prompted if this is a new character and to enter a password for it.</p>
                    <p>Next you'll have the race selection screen. If this is your first time playing Return of the Shadow it is highly recommended that you begin as a member of the free people of Middle-earth.</p>
                    <img className="play-img" src={RacePreview} />
                    <p>After you selected the race you wish to play, you'll be prompted to select a class. It is possible to created your own custom class, <Link to="/rcd">see the RotS Class Design page</Link>, however we do not recommend this for new players.</p>
                    <img className="play-img" src={ClassPreview} />
                    <p>You are now set to explore the world of Middle-earth and battle the forces of shadow or the free people!</p>
                    <img className="play-img" src={NewCharacter} />
                </div>
            </div>
        </div>
    </section>
</div>


